const firestore = firebase.firestore();

export const getMarkersFromFirestore = async () => {
  const markersRef = firestore.collection("markers").doc("userMarkers");
  const snapshot = await markersRef.get();
  return snapshot.exists ? snapshot.data().markers : [];
};

export const saveMarkersToFirestore = (newMarkers) => {
  const markersRef = firestore.collection("markers").doc("userMarkers");
  markersRef.set({ markers: newMarkers });
};
