import React, { useState, useRef, useEffect } from "react";
import { GoogleMap, Marker, LoadScript } from "@react-google-maps/api";
import {
  getMarkersFromFirestore,
  saveMarkersToFirestore,
} from "./FirebaseService";

const MapComponent = () => {
  const [markers, setMarkers] = useState([]);
  const mapRef = useRef(null);

  useEffect(() => {
    const fetchMarkers = async () => {
      const data = await getMarkersFromFirestore();
      setMarkers(data);
    };

    fetchMarkers();
  }, []);

  const handleMapClick = (event) => {
    const newMarker = {
      lat: event.latLng.lat(),
      lng: event.latLng.lng(),
    };

    const newMarkers = [...markers, newMarker];
    setMarkers(newMarkers);
    saveMarkersToFirestore(newMarkers);
  };

  const handleMarkerDragEnd = (index, event) => {
    const newMarkers = markers.map((marker, i) =>
      i === index
        ? { lat: event.latLng.lat(), lng: event.latLng.lng() }
        : marker
    );

    setMarkers(newMarkers);
    saveMarkersToFirestore(newMarkers);
  };

  const handleMarkerDelete = (index) => {
    const newMarkers = markers.filter((marker, i) => i !== index);

    setMarkers(newMarkers);
    saveMarkersToFirestore(newMarkers);
  };

  const handleDeleteAllMarkers = () => {
    setMarkers([]);
    saveMarkersToFirestore([]);
  };

  return (
    <LoadScript googleMapsApiKey="AIzaSyCJGCtoVkvL1R_OFzi06TsUyTWw9XdrZqs">
      <GoogleMap
        mapContainerStyle={{
          width: "100%",
          height: "400px",
        }}
        zoom={8}
        center={{
          lat: 50.4333,
          lng: 30.5166,
        }}
        onClick={handleMapClick}
        onLoad={(map) => (mapRef.current = map)}
      >
        {markers.map((marker, index) => (
          <Marker
            key={index}
            position={marker}
            label={(index + 1).toString()}
            onClick={() => handleMarkerDelete(index)}
            onDragEnd={(event) => handleMarkerDragEnd(index, event)}
            draggable={true}
          />
        ))}
      </GoogleMap>
      <button onClick={handleDeleteAllMarkers}>Delete All Markers</button>
    </LoadScript>
  );
};

export default MapComponent;
